}

#stripe {
  -webkit-animation: stripe-anim 3s linear infinite;
          animation: stripe-anim 3s linear infinite;
  -webkit-transform-origin: center;
          transform-origin: center;
  transform-box: fill-box;
}

@-webkit-keyframes stripe-anim {
  25% {
